import axios from "@/util/fetch.js";
import Header from "../nav/header.vue";
import Newadd from "../nav/newadd.vue";
export default {
  components:{
     Header,
     Newadd
  },   
  //
  beforeCreate: function() {
    document.getElementsByTagName("body")[0].className = "add_bg";
  },
  beforeDestroy: function() {
    document.body.removeAttribute("class", "add_bg");
  },
  name: "list",
  data() {
    return {
      values: "",
      active: "",
      typeData: [{ name: "",type:''}, { name: "",type:1 }, { name: "",type:2 },{ name: "",type:3 },{ name: "",type:4 },{ name: "",type:13 },{ name: "",type:14 },{ name: "",type:15 },{ name: "",type:16}],
      stateData: [{ name: "",status:''}, { name: "",status:2 }, { name: "",status:1 }],
      reviewlist: [],
      cuid:'',//UID
      keyword:'',//
      type:'',//
      limit:10,
      condition:{}
    };
  },
  
  mounted: function() {
      //
        this.condition={};
        this.condition.page=1;
        this.condition.limit=10;
        this.getData("/review/copybyme",this.condition,1)
  },

  methods: {
     // 
     getlist(ltype) {
        this.$refs.filt.style.display = "none";
        this.$refs.seek.style.display = "none";
        this.reviewlist = [];
        //
        this.condition={};
        this.condition.page=1;
        this.condition.limit=10;
        this.getData("/review/copybyme",this.condition,1)
    },
    
    // 
    showcondition1() {
        this.page=1;
        this.limit=5;
        this.keyword='';
        this.reviewlist = [];
        if (this.$refs.seek.style.display == "block") {
            this.$refs.seek.style.display = "none";
            this.$refs.filt.style.display = "none";
            this.keyword='';
        } else {
            this.$refs.seek.style.display = "block";
            this.$refs.filt.style.display = "none";
        }
        this.$refs.loadmore.style.display = "none";
    },

    // 
    showcondition2() {
        this.active='';
        this.reviewlist = [];
        if (this.$refs.filt.style.display == "block") {
            this.$refs.seek.style.display = "none";
            this.$refs.filt.style.display = "none";
        } else {
            this.$refs.seek.style.display = "none";
            this.$refs.filt.style.display = "block";
        }
        this.condition={};
        this.condition.page=1;
        this.condition.limit=10;
        this.getData("/review/copybyme",this.condition,1)
    },

    // 
    showtype(item,index) {
        this.$refs.loadmore.style.display = "none";
        this.active = index;
        this.reviewlist = [];
        this.condition={};
        this.condition.type=item.type;
        this.condition.page=1;
        this.condition.limit=10;
        this.getData("/review/copybyme",this.condition,1)
    },
    
    // 
    searchreview() {
        this.$refs.loadmore.style.display = "none";
        if(this.keyword==''){
            this.$Message.error("");
            return false;
        }
        this.reviewlist = [];
        this.condition={};
        this.condition.page=1;
        this.condition.limit=10;
        this.condition.keyword=this.keyword;
        this.getData("/review/copybyme",this.condition,1)
    },

    //
    loadmore() {
        this.$refs.loadmore.style.display = "none";
        this.condition.page += 1;
        this.getData("/review/copybyme",this.condition,0)
    },

    //axios
    getData(url,data,loadtype){
        console.log(data);
        //
        if(loadtype==0){
            this.$refs.dataloading.style.display = "block";
        }else{
            this.$refs.loading.style.display = "block"; 
        }
        //
        axios
            .post(url,data)
            .then(res => {  
                for (let i in res.data.list) {
                    this.reviewlist.push({
                        id: res.data.list[i].id,
                        apply_man: res.data.list[i].apply_man,
                        uid:res.data.list[i].uid,
                        sn:res.data.list[i].sn,
                        type:res.data.list[i].type,
                        lid:res.data.list[i].lid,
                        lname:res.data.list[i].lname,
                        start:res.data.list[i].start,
                        end:res.data.list[i].end,
                        period:res.data.list[i].period,
                        reason:res.data.list[i].reason,
                        status:res.data.list[i].status,
                        title:res.data.list[i].title,
                        content:res.data.list[i].content,
                        money:res.data.list[i].money,
                        address:res.data.list[i].address,
                        duty:res.data.list[i].duty,
                        number:res.data.list[i].number,
                        salary:res.data.list[i].salary,
                        intime:res.data.list[i].intime,
                        addtime:res.data.list[i].addtime,
                        endtime:res.data.list[i].endtime,
                        acid:res.data.list[i].acid
                    })
                }
                //
                if(res.data.count>this.limit && this.reviewlist.length<res.data.count){
                    this.$refs.loadmore.style.display = "block";
                }
                //
                if(loadtype==0){
                    this.$refs.dataloading.style.display = "none";
                }else{
                    this.$refs.loading.style.display = "none";
                } 
                //UID
                this.cuid=res.data.cuid;  
                console.log(res.data.count);  
                console.log(this.reviewlist.length);  
        });
    },

    // 
    getstatus(item) {
        if (item.status == 0) {
            return "";
        } else if (item.status == 1) {
            return "";
        } else if (item.status == 2) {
            return "";
        }else if (item.status == 3) {
            return "";
        }else{
            return "";
        }
    },

    //
    names(item) {
        if (item.apply_man.length == 2) {
            return item.apply_man;
        } else if (item.apply_man.length == 3) {
            return item.apply_man.substring(1, 3);
        } else if (item.apply_man.length == 4) {
            return item.apply_man.substring(2, 4);
        }
    },

    jump(item) {
      this.$router.push({
        name: "detail",
        params: {
          id: item.id,
          acid:item.acid,
          optype:3,
          block:sessionStorage.getItem("block")
        }
      })
    }
  }
}



