// axios
import axios from 'axios';

// axios
const httpService = axios.create({
    baseURL: 'http://woa.yangguangzhiye.cn', // 
    timeout: 3000 ,// 
});

// respone
httpService.interceptors.response.use(
    response => {
        // 
        const res = response;
        if (res.status != 200) {// 
            return Promise.reject({
                status: res.status,
                message: res.message
            });
        } else {
            return response;
        }
    },
    // 
    error => {
         if (error && error.response) {
            switch (error.response.status) {
                case 400:
                    error.message = '';
                    break;
                case 401:
                    error.message = '';
                    break;
                case 403:
                    error.message = '';
                    break;
                case 404:
                    error.message = ',';
                    break;
                case 405:
                    error.message = '';
                    break;
                case 408:
                    error.message = '';
                    break;
                case 500:
                    error.message = '';
                    break;
                case 501:
                    error.message = '';
                    break;
                case 502:
                    error.message = '';
                    break;
                case 503:
                    error.message = '';
                    break;
                case 504:
                    error.message = '';
                    break;
                case 505:
                    error.message = 'http';
                    break;
                default:
                    error.message = `${error.response.status}`;
            }
        } else {
            error.message = "";
        }
        return Promise.reject(error);
    }
)

/**/

/*
 *  get
 *  url:
 *  params:
 * */
export function get(url, params = {}) {
    return new Promise((resolve, reject) => {
        httpService({
            url: url,
            method: 'get',
            params: params
        }).then(response => {
            resolve(response);
        }).catch(error => {
            reject(error);
        });
    });
}

/*
 *  post
 *  url:
 *  params:
 * */
export function post(url, params = {}) {
    params['Login_token']= sessionStorage.getItem("Login_token");
    params['block']= sessionStorage.getItem("block");
    return new Promise((resolve, reject) => {
        httpService({
            url: url,
            method: 'post',
            data:params
        }).then(response => {
            resolve(response);
        }).catch(error => {
            reject(error);
        });
    });
}

/*
 *  
 *  url:
 *  params:
 * */
export function fileUpload(url, params = {}) {
    return new Promise((resolve, reject) => {
        httpService({
            url: url,
            method: 'post',
            data: params,
            headers: { 'Content-Type': 'multipart/form-data' }
        }).then(response => {
            resolve(response);
        }).catch(error => {
            reject(error);
        });
    });
}

export default {
    get,
    post,
    fileUpload
}


